 box-sizing: border-box;
}

body, html {
  font-family: "Cinzel", serif;
  padding: 0;
  margin: 0;
  width: 100%;
  height: 100%;
}

.skip-link {
  position: absolute;
  top: -40px;
  left: 0;
  padding: 8px;
  background-color: transparent;
  color: white;
  z-index: 100;
}

.skip-link:focus {
  top: 0;
}

a {
  font-size: 20px;
  text-decoration: none;
  padding: 10px;
  min-width: 44px;
  min-height: 44px;
  color: white;
}

nav {
  width: 100%;
  background-color: #680a10;
  color: white;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  justify-content: center;
  align-items: center;

  z-index: 1000;
}

.nav-title {
  font-size: 4vh;
  font-weight: 400;
  margin-left: 40px;
}

.nav-body {
  display: flex;
  justify-content: center;
  margin: 20px;
}

li {
  list-style-type: none;
  margin: 0 20px 0 20px;
  transition: 300ms all ease;
}

li:hover {
  border-inline: none;
  border-block-start: none;
  border-block-end: 2px solid white;
  transition: 300ms all ease;
}

.nav-item {
  min-height: 44px;
  min-width: 44px;
}

button {
  display: block;
  background-color: transparent;
  border-radius: 10px;
  min-width: 44px;
  min-height: 44px;
  border: 0;
  font-size: 44px;
  color: white;
  cursor: pointer;
}

/* start of main */
main {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero-container {
  width: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-color: green;
  background-position: center;

  width: 100%;
  min-height: 380px;
  margin-top: 20px;
  border-radius: 5px;

  display: flex;
  justify-content: center;
  align-items: center;
}

.hero-caption {
  background-color: #85858585;
  backdrop-filter: blur(3px);
  padding: 40px;
  margin: 40px;
  border-radius: 20px;

  text-align: center;
  font-family: "Square Peg", cursive;
  font-weight: 500;
  font-size: 6vh;
}

.list-resto {
  display: grid;
  gap: 10px;
  padding: 30px;

  width: 100%;
}

.card {
  display: flex;
  flex-direction: column;
  background-color: white;
  width: 100%;

  border-radius: 10px;
  box-shadow: 5px 2px 10px #75737378;

  font-family: "Montserrat", sans-serif;
  transition: 300ms all ease-in-out;
}

.card:hover {
  transform: translateY(-5px);
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
  transition: 300ms all ease-in-out;
}

.card .resto-img {
  width: 100%;
  height: 200px;
  border-radius: 10px 10px 0 0;
}

.card .card-title {
  margin: 10px 0 0 30px;
}

.card .card-body {
  display: -webkit-box;
  overflow: hidden;
  text-overflow: ellipsis;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 7; /* number of lines to show */
  text-align: justify;
  margin: 0 7px 5px 7px;
}

/*
  Like
*/
.like {
  width: 55px;
  height: 55px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #db0000;
  
  position: fixed;
  bottom: 16px;
  right: 16px;
  border-radius: 50%;
  border: 0;
  
  font-size: 18px;
  color: white;
  cursor: pointer;
}

/* end of main */

footer {
  display: grid;
  grid-template-columns: repeat(2, 1fr);

  background-color: #680a10;
  width: 100%;
  padding: 20px;
  align-items: center;
}

.linkedin {
  font-size: 3vh;
  min-width: 44px;
  min-height: 44px;
}

.footer-caption {
  display: flex;
  justify-content: right;
  color: white;
}`, "",{"version":3,"sources":["webpack://./src/styles/main.css"],"names":[],"mappings":"AAAA;CACC,sBAAsB;AACvB;;AAEA;EACE,4BAA4B;EAC5B,UAAU;EACV,SAAS;EACT,WAAW;EACX,YAAY;AACd;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,OAAO;EACP,YAAY;EACZ,6BAA6B;EAC7B,YAAY;EACZ,YAAY;AACd;;AAEA;EACE,MAAM;AACR;;AAEA;EACE,eAAe;EACf,qBAAqB;EACrB,aAAa;EACb,eAAe;EACf,gBAAgB;EAChB,YAAY;AACd;;AAEA;EACE,WAAW;EACX,yBAAyB;EACzB,YAAY;EACZ,aAAa;EACb,qCAAqC;EACrC,uBAAuB;EACvB,mBAAmB;;EAEnB,aAAa;AACf;;AAEA;EACE,cAAc;EACd,gBAAgB;EAChB,iBAAiB;AACnB;;AAEA;EACE,aAAa;EACb,uBAAuB;EACvB,YAAY;AACd;;AAEA;EACE,qBAAqB;EACrB,qBAAqB;EACrB,0BAA0B;AAC5B;;AAEA;EACE,mBAAmB;EACnB,wBAAwB;EACxB,iCAAiC;EACjC,0BAA0B;AAC5B;;AAEA;EACE,gBAAgB;EAChB,eAAe;AACjB;;AAEA;EACE,cAAc;EACd,6BAA6B;EAC7B,mBAAmB;EACnB,eAAe;EACf,gBAAgB;EAChB,SAAS;EACT,eAAe;EACf,YAAY;EACZ,eAAe;AACjB;;AAEA,kBAAkB;AAClB;EACE,WAAW;EACX,cAAc;EACd,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,WAAW;EACX,cAAc;EACd,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,yDAA8D;EAC9D,uBAAuB;EACvB,2BAA2B;;EAE3B,WAAW;EACX,iBAAiB;EACjB,gBAAgB;EAChB,kBAAkB;;EAElB,aAAa;EACb,uBAAuB;EACvB,mBAAmB;AACrB;;AAEA;EACE,2BAA2B;EAC3B,0BAA0B;EAC1B,aAAa;EACb,YAAY;EACZ,mBAAmB;;EAEnB,kBAAkB;EAClB,kCAAkC;EAClC,gBAAgB;EAChB,cAAc;AAChB;;AAEA;EACE,aAAa;EACb,SAAS;EACT,aAAa;;EAEb,WAAW;AACb;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,uBAAuB;EACvB,WAAW;;EAEX,mBAAmB;EACnB,kCAAkC;;EAElC,qCAAqC;EACrC,iCAAiC;AACnC;;AAEA;EACE,2BAA2B;EAC3B,2CAA2C;EAC3C,iCAAiC;AACnC;;AAEA;EACE,WAAW;EACX,aAAa;EACb,4BAA4B;AAC9B;;AAEA;EACE,qBAAqB;AACvB;;AAEA;EACE,oBAAoB;EACpB,gBAAgB;EAChB,uBAAuB;EACvB,4BAA4B;EAC5B,qBAAqB,EAAE,4BAA4B;EACnD,mBAAmB;EACnB,qBAAqB;AACvB;;AAEA;;CAEC;AACD;EACE,WAAW;EACX,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,yBAAyB;;EAEzB,eAAe;EACf,YAAY;EACZ,WAAW;EACX,kBAAkB;EAClB,SAAS;;EAET,eAAe;EACf,YAAY;EACZ,eAAe;AACjB;;AAEA,gBAAgB;;AAEhB;EACE,aAAa;EACb,qCAAqC;;EAErC,yBAAyB;EACzB,WAAW;EACX,aAAa;EACb,mBAAmB;AACrB;;AAEA;EACE,cAAc;EACd,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,YAAY;AACd